                    {time: '2018-10 ', company: '', post: 'Web ', size: 'large', color: '#4285F4'},
                    {time: '2017-10  2018-09', company: '', post: 'Web ', color: '#FBBC05'},
                    // {time: '2018-03  2018-09', company: '', post: 'Web ', color: '#EA4335'},
                    // {time: '2017-10  2018-02', company: '', post: 'Web ', color: '#FBBC05'},
                    {time: '2015-12  2017-09', company: '', post: 'Web ', color: '#34A853'},
                ];
                */
                self.workExperienceData = [
                    {time: 'YYYY-MM ', company: 'AAAAAAAAAAAA', post: 'Web ', size: 'large', color: '#4285F4'},
                    {time: 'YYYY-MM  YYYY-MM', company: 'BBBBBBBBBBBBB', post: 'Web ', color: '#FBBC05'},
                    {time: 'YYYY-MM  YYYY-MM', company: 'CCCCCCCCCC', post: 'Web ', color: '#34A853'},
                ];
            },
            getProjectData(){
                let self = this;
                self.projectData = [
                    {time: '2020-05 ', name: '', post: 'web ', discript: '', workContent: '1 story  todoList story web Code Review<br/>2 <b>Vue</b>  <b>Element UI</b> ', size: 'large', color: '#4285F4'},
